body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  background-color: #282a36;
  margin-top: 10px;

  
}


.appContainer {
  background-color: #44475a;
  width: 80vw;
  height: auto;
  border-radius: 25px;
  padding-left: 10px;
  padding-right: 10px;
  padding-bottom: 25px;
  margin-bottom: 10px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  flex-direction: column;
  border: solid 0.5px #6272a4;

  @media screen and (max-width: 700px) {
    width: 85vw;
    
    
  }
}


.toDoBoard {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 2em;
  
}

.toDo {
  background-color: #f1fa8c;
  width: 9em;
  height: 9em;

  box-shadow: #282a36 0px 20px 30px -10px;
  font-size: 2em;
  padding: 10px;
  transform:rotate(-1.5deg);
  transition: transform .15s linear;
  white-space: pre-wrap;
  word-wrap: break-word;

  @media screen and (max-width: 450px) {
    width: 7.5em;
    height: 7.5em;
  }

  @media screen and (max-width: 350px) {
    width: 6.5em;
    height: 6.5em;
  }

    
}

.toDo:hover {
  transition: transform .15s linear;
  transform:scale(1.1);
  position:relative;
  z-index: 10;
}

.trashicon{
  color: grey;
  opacity: 50%;
  position: fixed;
  bottom: 0em;
  right: 0.25em;
  z-index: 999;
}

.trashicon:hover{
  color: black;
  opacity: 100%;
  cursor: pointer;
}

.toDo:nth-child(even) {
  background-color: #8be9fd;
  transform:rotate(3.5deg);
  position:relative;
  top:5px;
}

.toDo:nth-child(even):hover {
  transition: transform .15s linear;
  transform:scale(1.1);
  position:relative;
  z-index: 10;
}

.toDo:nth-child(3n) {
  background-color: #bd93f9;
  transform:rotate(-2deg);
  position:relative;
  top:-5px;

}

.toDo:nth-child(3n):hover {
  transition: transform .15s linear;
  transform:scale(1.1);
  position:relative;
  z-index: 10;
}

.toDo:nth-child(4n) {
  background-color: #50fa7b;
  transform:rotate(3deg);
  position:relative;
  top:-10px;

}

.toDo:nth-child(4n):hover {
  transition: transform .15s linear;
  transform:scale(1.1);
  position:relative;
  z-index: 10;
}

.toDo:nth-child(6n) {
  background-color: #8be9fd;
  transform:rotate(2.5deg);
  position:relative;
  top:5px;

}

.toDo:nth-child(6n):hover {
  transition: transform .15s linear;
  transform:scale(1.1);
  position:relative;
  z-index: 10;
}


h1 {
  color: #ff79c6;
  padding-bottom: 0.1em;
  
}

.inputField{
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1em;
  padding-bottom: 2.5em;
}

button{
  border-radius: 50%;
  border: none;
  background-color: #ff79c6;
  color: #ffff;
  height:2em;
  width: 2em;
  font-size: x-large;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 0;
  transition: transform .15s linear;

}

button.cornerButton{
  background-color: ff79c6;
  position: fixed;
  bottom: 0;
  right: 0;
  margin: 30px;
  z-index: 1000;
}

button:hover{
  cursor: pointer;
  transform: scale(1.1);
  transition: transform .15s linear;
} 
  

.relax{
  color: white;
  text-align: center;
}
  



